.tin-tuc {
	padding: 60px 0;
	background: $color-4;
	line-height: 1.5;
	.title {
		color: $color-title-3;
		font-size: 32px;
		margin-bottom: 10px; }
	.text-center {
		margin-bottom: 30px; }
	.box-img {
		height: 125px;
		overflow: hidden;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	.swiper-container {
		padding: 30px 0;
		margin-top: -30px; }
	.swiper-slide {
		transition: all .15s ease-in-out;
		&:hover {
			transform: scale(1.1); } }
	.article-title {
		font-weight: 900;
		color: $color-title-3;
		overflow: hidden;
		display: -webkit-box;
		text-overflow: ellipsis;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical; }
	.item-content {
		margin-top: 5px;
		font-size: 15px;
		overflow: hidden;
		display: -webkit-box;
		text-overflow: ellipsis;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical; }
	.pagination {
		text-align: center;
		margin: 0;
		span {
			width: 8px;
			height: 8px;
			margin: 0 5px;
			background: $color-text;
			opacity: 1;
			position: relative;
			outline: none;
			&:before {
				content: '';
				width: 20px;
				height: 20px;
				position: absolute;
				top: 50%;
				left: 50%;
				z-index: -1;
				border-radius: 99px;
				transform: translate(-50%, -50%); } }
		.swiper-pagination-bullet-active {
			background: $color-title-2;
			&:before {
				z-index: 1;
				border: 1px solid $color-title-2; } } }
	.item {
		display: flex;
		padding: 10px 0;
		padding-left: 15px;
		border-top: 1px solid $color-1;
		.posted {
			margin-top: 0; }
		&-title {
			font-size: 15px;
			margin-left: 15px;
			overflow: hidden;
			display: -webkit-box;
			text-overflow: ellipsis;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical; }
		&:hover {
			background: $color-title-3;
			color: #fff;
			transition: all .2s;
			border-color: $color-title-3;
			// a
 }			// 	font-weight: 700
		&:last-child {
			border-bottom: 1px solid $color-1; } } }
@media (max-width: 991.97px) {
	.tin-tuc {
		.pagination {
			margin-bottom: 20px; } } }
@media (max-width: 767.97px) {
	.tin-tuc {
		.title {
			font-size: 24px; }
		.text-center {
			margin-bottom: 15px; } }
	.dieuduong-tinchinh {
		[class*="col-"] {
			order: 1; }
		.akhj {
			order: 0; } } }
@media screen and ( min-width: 768px ) {
	.dieuduong-tinchinh {
		margin-top: 24px;
		margin-bottom: 32px; }
	.dieuduong-main {
		.box-img {
			height: 200px; } }
	.akhj {
		.swiper-container {
			padding: 0; } } }
