$color-title-1: #9F1C6A;
$color-title-2: #11CFE7;
$color-title-3: #D3398D;
$color-title-4: #FBEBF3;
$color-sub-title: #FFA502;
$color-text: #707070;
$color-1: #CDCDCD;
$color-2: #F7F7F7;
$color-3: #EBEBEB;
$color-4: #F3F7FA;
$color-low: #009933;
$color-guard: #0066FF;
$color-elevate: #FFFF00;
$color-warning: #FF9900;
$color-severe: #FF0000;
$color-money: #2B76AE;

::selection {
	color: #fff;
	background: $color-title-1; }
::-moz-selection {
	color: #fff;
	background: $color-title-1; }

body {
	font-family: 'Roboto', sans-serif;
	color: $color-text;
	font-weight: 400;
	font-size: 16px;
	overflow-x: hidden; }
a {
	color: inherit;
	text-decoration: none; }
li {
	list-style: none; }
.title {
	font-weight: 900;
	font-size: 28px;
	line-height: 1.5;
	text-transform: uppercase;
	color: $color-title-1; }
.sub-title {
	font-weight: 700;
	font-size: 20px;
	text-transform: uppercase; }
.text-center {
	text-align: center; }
.see-more {
	display: inline-block;
	text-transform: uppercase;
	padding: 0 20px;
	border: 1px solid $color-text;
	border-radius: 99px;
	line-height: 40px;
	&:after {
		content: '\F054';
		display: inline-block;
		font-family: 'Material Design Icons';
		font-size: inherit;
		text-rendering: auto;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		transform: translateY(10%); } }
.posted {
	margin-top: 7px;
	padding-left: 15px;
	font-size: 12px;
	position: relative;
	&:before {
		content: '\e863';
		font-family: 'Linearicons-Free';
		speak: none;
		font-style: normal;
		font-weight: normal;
		font-variant: normal;
		text-transform: none;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		-moz-osx-font-smoothing: grayscale;
		position: absolute;
		top: 2px;
		left: 0; } }
.line {
	background: $color-1;
	margin: 10px 0;
	width: 100%;
	height: 2px; }
.form-item {
	width: 100%;
	margin-bottom: 10px;
	label {
		color: $color-title-1; }
	input, textarea {
		margin-top: 7px;
		width: 100%;
		padding: 8px 15px;
		background: rgba(#fff, .5);
		border: 1px solid $color-title-3;
		border-radius: 4px;
		font-family: 'Roboto', sans-serif;
		font-weight: 700;
		color: $color-title-1;
		&::placeholder {
			color: $color-title-3;
			font-weight: 400;
			font-style: italic; } }
	textarea {
		height: 150px; } }
.flex-box {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: flex-start; }
.flex-space-around {
	display: flex;
	align-items: center;
	justify-content: space-around;
	flex-wrap: wrap; }
