.header-banner {
	line-height: 1.5;
	height: 660px;
	overflow: hidden;
	.figure {
		margin-top: 30px;
		.box-img {
			text-align: right; } }
	.banner-right {
		z-index: 1;
		&:before {
			content: '';
			width: 2000px;
			height: 650px;
			background-image: url(../img/header-banner-bg-2.png);
			background-size: cover;
			position: absolute;
			left: -50%;
			top: 0;
			z-index: -1; } }
	.title {
		margin: 100px 0 30px 15px; }
	.sub-title {
		margin-top: 10px;
		margin-left: 50px;
		color: $color-sub-title; }
	.swiper-slide {
		.box-img {
			position: relative;
			width: 300px;
			height: 250px;
			overflow: hidden;
			margin-top: 10px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; }
			&:before {
				content: '';
				width: 300px;
				height: 250px;
				background-image: url(../img/border-item.png);
				background-size: contain;
				position: absolute;
				left: 0;
				top: 0; } } }
	.pagination {
		span {
			border-radius: 0;
			width: 30px;
			height: 5px;
			margin: 0 5px;
			outline: none;
			&:first-child {
				margin-left: 0; }
			&.swiper-pagination-bullet-active {
				background: $color-title-1; } } }
	.swiper-slide p,
	.pagination,
	.see-more {
		margin-left: 50px; }
	.see-more {
		margin-top: 15px; } }
@media (max-width: 991.97px) {
	.header-banner {
		.figure {
			margin-top: 100px;
			.box-img {
				img {
					width: 100%; } } } } }
@media (max-width: 767.97px) {
	.header-banner {
		height: auto;
		text-align: center;
		.title,
		.sub-title {
			margin-left: auto;
			margin-right: auto; }
		.sub-title {
			margin-top: 16px; }
		.figure {
			margin-top: 30px; }
		.swiper-slide {
			.box-img {
				margin: 0 auto;
				margin-top: 16px; }
			img {
				height: 450px; } }
		.swiper-slide p,
		.pagination,
		.see-more {
			margin-left: 0; }
		.pagination {
			span:first-child {
				margin-left: 4px; } }
		.banner-right {
			order: -1;
			&:before {
				width: 1000px;
				left: -35%;
				height: 450px; } } } }
@media (max-width: 575.97px) {
	.header-banner {
		.figure {
			margin-top: 40px;
			img {
				height: auto; } }
		.banner-right {
			z-index: -1; } } }
